*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}

nav{
  background-color: lightcoral;
  color: white;
  padding: 20px;
  text-align: center;
  font-size: 2.5rem;
}

.container{
  background-color: lightblue;
  width: 70%;
  margin: auto;
  min-height: 100vh;
  padding: 4vmax;
}

.container form{
  display: flex;
  flex-direction: column;
}

.container form input,
.container form textarea,
.container form button{
  padding: 1.5rem;
  border: 1px solid black;
  border-radius: 5px;
  margin: 1rem;
  font-size: 1.25rem;
}

button{
  /* background-color: yellow; */
  border: 1px solid black;
}

.container form button{
  border: none;
  cursor: pointer;
  background-color: aqua;
  color: black;
}

.task{
  width: 97%;
  min-height: 4rem;
  margin: auto;
  margin: 1rem;
  display: flex;
  font-size: 1.5rem;
  align-items: center;
  display: flex;
  background-color: white;
  padding: 1.5rem;  /* justify-content: space-evenly; */
}


.task div{
  width: 100%;
}

.task button{
  border-radius: 100%;
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  margin-right: 1.25rem;
}

.desc{
  font-size: 1rem;
  margin-top: 0.75rem;
}